.contactsList {
    align-items: start;
    box-shadow: 2px 2px 10px 2px rgba(0, 0, 0, 0.562);
    transition: transform 250ms cubic-bezier(0.17, 0.67, 0.83, 0.67),
        box-shadow 150ms cubic-bezier(0.17, 0.67, 0.83, 0.67);
}

.phoneNumber {
    font-weight: 500;
    color: rgb(9, 80, 223);
}

.contactName {
    width: 100%;
    font-weight: 500;
    display: flex;
}

.listItem {
    display: flex;
    border: 1px solid black;
    border-radius: 4px;
    padding: 10px;
    background-color: rgba(255, 239, 157, 0.45);
    flex-wrap: wrap;
    align-items: flex-end;
}

.listItem:not(:last-child) {
    margin-bottom: 2px;
}

.buttons {
    margin-left: auto;
    color: grey;
    background-color: rgba(255, 239, 157, 0.45);
    border: hidden;
    transform: scale(1);
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
        transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.buttons:hover {
    color: red;
    transform: scale(1.3);
}